  .tooltip .tooltiptext {
    background-color: rgb(0, 0, 0);
    border-radius: 6px;
    color: rgb(255, 255, 255);
    font-size: 14px;
    left: 50%;
    margin-left: -60px;
    opacity: 0;
    padding: 5px 0px;
    position: absolute;
    text-align: center;
    top: 125%;
    transition: opacity 1s ease 0s;
    visibility: hidden;
    width: 175px;
    z-index: 1;
  }

  .tooltip:hover .tooltiptext {
    font-size: 14px;
    opacity: 1;
