  .kg-product-card,
  .kg-product-card * {
    box-sizing: border-box;
  }

  .kg-product-card {
    display: flex;
    align-items: center;
    flex-direction: column;
    width: 100%;
  }

  .kg-product-card-container {
    display: grid;
    grid-template-columns: auto min-content;
    align-items: center;
    row-gap: 16px;
    background: 0px 0px;
    max-width: 550px;
    padding: 20px;
    width: 100%;
    border-radius: 5px;
    box-shadow: rgba(125, 140, 155, 0.25) 0px 0px 0px 1px inset;
  }

  .kg-product-card-image {
    grid-column: 1 / 3;
    justify-self: center;
  }

  .kg-product-card-title-container {
    grid-column: 1 / 2;
  }

  .kg-product-card h4.kg-product-card-title {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
    text-decoration: none;
    font-weight: 700;
    font-size: 1.4em;
    margin-top: 0px;
    margin-bottom: 0px;
    line-height: 1.15em;
  }

  .kg-product-card-description {
    grid-column: 1 / 3;
  }

  .kg-product-card .kg-product-card-description ol,
  .kg-product-card .kg-product-card-description p,
  .kg-product-card .kg-product-card-description ul {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
    font-size: 0.95em;
    line-height: 1.5em;
    opacity: 0.7;
    margin-bottom: 0px;
  }

  .kg-product-card .kg-product-card-description p:first-of-type {
    margin-top: -4px;
  }

  .kg-product-card .kg-product-card-description ol,
  .kg-product-card .kg-product-card-description p:not(:first-of-type),
  .kg-product-card .kg-product-card-description ul {
    margin-top: 0.95em;
  }

  .kg-product-card .kg-product-card-description li+li,
  .kg-toggle-card li+li {
    margin-top: 0.5em;
  }

  .kg-product-card-rating {
    display: flex;
    align-items: center;
    grid-column: 2 / 3;
    place-self: start end;
    padding-left: 16px;
  }

  @media (max-width: 400px) {
    .kg-product-card-title-container {
      grid-column: 1 / 3;
    }

    .kg-product-card-rating {
      grid-column: 1 / 3;
      justify-self: start;
      margin-top: -15px;
      padding-left: 0px;
    }
  }

  .kg-product-card-rating-star {
    height: 28px;
    width: 20px;
  }

  .kg-product-card-rating-star svg {
    width: 16px;
    height: 16px;
    fill: currentcolor;
    opacity: 0.15;
  }

  .kg-product-card a.kg-product-card-button {
    justify-content: center;
    grid-column: 1 / 3;
    display: flex;
    position: static;
    align-items: center;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
    font-size: 0.95em;
    font-weight: 600;
    line-height: 1em;
    text-decoration: none;
    width: 100%;
    height: 2.4em;
    border-radius: 5px;
    padding: 0px 1.2em;
    transition: opacity 0.2s ease-in-out 0s;
  }

  .kg-product-card a.kg-product-card-btn-accent {
    background-color: var(--ghost-accent-color-night-eye);
    color: rgb(255, 255, 255);
  }

  .kg-toggle-card,
  .kg-toggle-card * {
    box-sizing: border-box;
  }

  .kg-toggle-card {
    background: 0px 0px;
    box-shadow: rgba(125, 140, 155, 0.25) 0px 0px 0px 1px inset;
    border-radius: 4px;
    padding: 1.2em;
  }

  .kg-toggle-card[data-kg-toggle-state="close"] .kg-toggle-content {
    height: 0px;
    overflow: hidden;
    transition: opacity 0.5s ease 0s, top 0.35s ease 0s;
    opacity: 0;
    top: -0.5em;
    position: relative;
  }

  .kg-toggle-content {
    height: auto;
    opacity: 1;
    transition: opacity 1s ease 0s, top 0.35s ease 0s;
    top: 0px;
    position: relative;
  }

  .kg-toggle-card[data-kg-toggle-state="close"] svg {
    transform: unset;
  }

  .kg-toggle-heading {
    cursor: pointer;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
  }

  .kg-toggle-card h4.kg-toggle-heading-text {
    font-size: 1.15em;
    font-weight: 700;
    line-height: 1.3em;
    margin-top: 0px;
    margin-bottom: 0px;
  }

  .kg-toggle-content p:first-of-type {
    margin-top: 0.5em;
  }

  .kg-toggle-card .kg-toggle-content ol,
  .kg-toggle-card .kg-toggle-content p,
  .kg-toggle-card .kg-toggle-content ul {
    font-size: 0.95em;
    line-height: 1.5em;
    margin-top: 0.95em;
    margin-bottom: 0px;
  }

  .kg-toggle-card-icon {
    height: 24px;
    width: 24px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 1em;
    background: 0px 0px;
    border: 0px;
  }

  .kg-toggle-heading svg {
    width: 14px;
    color: rgba(183, 191, 200, 0.5);
    transition: all 0.3s ease 0s;
    transform: rotate(-180deg);
  }

  .kg-toggle-heading path {
    fill: none;
    stroke: currentcolor;
    stroke-linecap: round;
    stroke-linejoin: round;
    stroke-width: 1.5;
    fill-rule: evenodd;
  }

  .kg-toggle-card+.kg-toggle-card {
    margin-top: 1em;
  }

  .kg-video-card,
  .kg-video-card * {
    box-sizing: border-box;
  }

  .kg-video-card {
    position: relative;
    --seek-before-width: 0%;
    --volume-before-width: 100%;
    --buffered-width: 0%;
    --seek-before-width-night-eye: 0%;
    --volume-before-width-night-eye: 100%;
    --buffered-width-night-eye: 0%;
  }

  .kg-video-card video {
    display: block;
    max-width: 100%;
    height: auto;
  }

  .kg-video-container {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .kg-video-large-play-icon,
  .kg-video-overlay {
    display: flex;
    justify-content: center;
    align-items: center;
    transition: opacity 0.2s ease-in-out 0s;
  }

  .kg-video-overlay {
    position: absolute;
    top: 0px;
    right: 0px;
    bottom: 0px;
    left: 0px;
    background-image: linear-gradient(rgba(0, 0, 0, 0.3) 0px, transparent 70%, transparent 100%);
    z-index: 999;
  }

  .kg-video-large-play-icon {
    width: 72px;
    height: 72px;
    padding: 0px;
    background: rgba(0, 0, 0, 0.5);
    border-radius: 50%;
  }

  .kg-video-large-play-icon svg {
    width: 20px;
    height: auto;
    margin-left: 2px;
    fill: rgb(255, 255, 255);
  }

  .kg-video-player-container {
    position: absolute;
    bottom: 0px;
    width: 100%;
    height: 80px;
    background: linear-gradient(transparent, rgba(0, 0, 0, 0.5));
    z-index: 999;
    transition: opacity 0.2s ease-in-out 0s;
  }

  .kg-video-player {
    position: absolute;
    bottom: 0px;
    display: flex;
    align-items: center;
    width: 100%;
    z-index: 9999;
    padding: 12px 16px;
  }

  .kg-video-current-time,
  .kg-video-time {
    font-family: inherit;
    font-size: 0.85em;
    font-weight: 500;
    line-height: 1.4em;
    white-space: nowrap;
  }

  .kg-video-current-time {
    min-width: 38px;
    padding: 0px 4px;
    color: rgb(255, 255, 255);
  }

  .kg-video-time {
    color: rgba(255, 255, 255, 0.6);
  }

  .kg-video-duration {
    padding: 0px 4px;
  }

  .kg-video-pause-icon,
  .kg-video-play-icon {
    position: relative;
    padding: 0px 4px 0px 0px;
    font-size: 0px;
    background: 0px 0px;
  }

  .kg-video-hide {
    display: none !important;
  }

  .kg-video-hide-animated {
    transition: opacity 0.2s ease-in-out 0s;
    cursor: initial;
    opacity: 0 !important;
  }

  .kg-video-pause-icon svg,
  .kg-video-play-icon svg {
    width: 14px;
    height: 14px;
    fill: rgb(255, 255, 255);
  }

  .kg-video-seek-slider {
    flex-grow: 1;
    margin: 0px 4px;
  }

  @media (max-width: 520px) {
    .kg-video-seek-slider {
      display: none;
    }
  }

  .kg-video-playback-rate {
    min-width: 37px;
    padding: 0px 4px;
    color: rgb(255, 255, 255);
    font-family: inherit;
    font-size: 0.85em;
    font-weight: 600;
    line-height: 1.4em;
    text-align: left;
    background: 0px 0px;
    white-space: nowrap;
  }

  @media (max-width: 520px) {
    .kg-video-playback-rate {
      padding-left: 8px;
    }
  }

  .kg-video-mute-icon,
  .kg-video-unmute-icon {
    position: relative;
    bottom: -1px;
    padding: 0px 4px;
    font-size: 0px;
    background: 0px 0px;
  }

  @media (max-width: 520px) {

    .kg-video-mute-icon,
    .kg-video-unmute-icon {
      margin-left: auto;
    }
  }

  .kg-video-mute-icon svg,
  .kg-video-unmute-icon svg {
    width: 16px;
    height: 16px;
    fill: rgb(255, 255, 255);
  }

  .kg-video-volume-slider {
    width: 80px;
  }

  @media (max-width: 300px) {
    .kg-video-volume-slider {
      display: none;
    }
  }

  .kg-video-seek-slider::before,
  .kg-video-volume-slider::before {
    content: "";
    position: absolute;
    left: 0px;
    height: 4px;
    cursor: pointer;
    background-color: rgb(43, 43, 43);
    border-radius: 2px;
    width: var(--seek-before-width) !important;
  }

  .kg-video-volume-slider::before {
    width: var(--volume-before-width) !important;
  }

  .kg-video-card input[type="range"] {
    position: relative;
    -webkit-appearance: none;
    background: 0px 0px;
    height: auto;
    padding: 0px;
    border: 0px;
  }

  .kg-video-card input[type="range"]::-webkit-slider-thumb {
    -webkit-appearance: none;
  }

  .kg-video-card input[type="range"]::-webkit-slider-runnable-track {
    width: 100%;
    height: 4px;
    cursor: pointer;
    background: rgba(28, 28, 33, 0.2);
    border-radius: 2px;
  }

  .kg-video-card input[type="range"]::-webkit-slider-thumb {
    position: relative;
    box-sizing: content-box;
    width: 13px;
    height: 13px;
    margin: -5px 0px 0px;
    border: 0px;
    cursor: pointer;
    background: rgb(28, 28, 33);
    border-radius: 50%;
    box-shadow: rgba(0, 0, 0, 0.08) 0px 0px 0px 1px, rgba(0, 0, 0, 0.24) 0px 1px 4px;
  }

  .kg-video-card input[type="range"]:active::-webkit-slider-thumb {
    transform: scale(1.2);
  }

  code[class*="language-"],
  pre[class*="language-"] {
    color: rgb(204, 204, 204);
    background: 0px 0px;
    font-family: Consolas, Monaco, "Andale Mono", "Ubuntu Mono", monospace;
    font-size: 1em;
    text-align: left;
    white-space: pre;
    word-spacing: normal;
    word-break: normal;
    overflow-wrap: normal;
    line-height: 1.5;
    tab-size: 4;
    hyphens: none;
  }

  pre[class*="language-"] {
    padding: 1em;
    margin: 0.5em 0px;
    overflow: auto;
  }

  :not(pre)>code[class*="language-"],
  pre[class*="language-"] {
    background: rgb(46, 46, 46);
  }

  :not(pre)>code[class*="language-"] {
    padding: 0.1em;
    border-radius: 0.3em;
    white-space: normal;
  }

  .token.block-comment,
  .token.cdata,
  .token.comment,
  .token.doctype,
  .token.prolog {
    color: rgb(191, 191, 191);
  }

  .token.punctuation {
    color: rgb(204, 204, 204);
  }

  .token.attr-name,
  .token.deleted,
  .token.namespace,
  .token.tag {
    color: rgb(230, 153, 156);
  }

  .token.function-name {
    color: rgb(159, 191, 224);
  }

  .token.boolean,
  .token.function,
  .token.number {
    color: rgb(230, 184, 153);
  }

  .token.class-name,
  .token.constant,
  .token.property,
  .token.symbol {
    color: rgb(230, 205, 153);
  }

  .token.atrule,
  .token.builtin,
  .token.important,
  .token.keyword,
  .token.selector {
    color: rgb(212, 170, 213);
  }

  .token.attr-value,
  .token.char,
  .token.regex,
  .token.string,
  .token.variable {
    color: rgb(166, 216, 185);
  }

  .token.entity,
  .token.operator,
  .token.url {
    color: rgb(159, 223, 222);
  }

  .token.bold,
  .token.important {
    font-weight: 700;
  }

  .token.italic {
    font-style: italic;
  }

  .token.entity {
    cursor: help;
  }

  .token.inserted {
    color: rgb(153, 230, 153);
  }

  :root {
    --ghost-accent-color: hsla(340, 60%, 75%, 1);
    --ghost-accent-color-night-eye: hsla(340, 60%, 59%, 1);
  }

  .App {
    text-align: center;
  }

  .App-logo {
    height: 40vmin;
    pointer-events: none;
  }

  @media (prefers-reduced-motion: no-preference) {
    .App-logo {
      animation: 20s linear 0s infinite normal none running App-logo-spin;
    }
  }

  .App-header {
    background-color: rgb(40, 44, 52);
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: rgb(255, 255, 255);
  }

  .App-link {
    color: rgb(153, 213, 230);
  }

  @-webkit-keyframes App-logo-spin {
    0% {
      transform: rotate(0deg);
    }

    100% {
      transform: rotate(360deg);
    }
  }

  @keyframes App-logo-spin {
    0% {
      transform: rotate(0deg);
    }

    100% {
      transform: rotate(360deg);
    }
  }

  .fluid-width-video-wrapper {
    width: 100%;
    position: relative;
    padding: 0px;
  }

  .fluid-width-video-wrapper iframe,
  .fluid-width-video-wrapper object,
  .fluid-width-video-wrapper embed {
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
  }
